* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-weight: 400;
}

body {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
  color: #333;
}

.container {
  width: 90vw;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.tile {
  border: 1px solid #ccc;
  border-radius: 1rem;
  cursor: pointer;
  user-select: none;
  text-align: center;
}

.tile img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.tile p {
  font-weight: 100;
  line-height: 2;
}

.tile:hover {
  opacity: 0.7;
  transition: all 0.3s;
  transform: translateY(-3px);
}
